import { request } from 'utils';

const prefix = '/api/categories';

export const getDisciplines = ({ label, page, pageSize = 10, orderBy, isPagingEnabled = true }) =>
  request({
    prefix,
    url: '/Discipline',
    method: 'GET',
    params: { label, page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });

export const getDisciplineById = id =>
  request({
    prefix,
    url: `/Discipline/${id}`,
    method: 'GET',
  });

export const deleteDiscipline = id =>
  request({
    prefix,
    url: `/Discipline/${id}`,
    method: 'DELETE',
  });

export const updateDiscipline = ({ id, label, level }) =>
  request({
    prefix,
    url: `/Discipline/${id}`,
    data: { id, label, level },
    method: 'PUT',
  });

export const createDiscipline = ({ label, level }) =>
  request({
    prefix,
    url: `/Discipline`,
    data: { label, level },
    method: 'POST',
  });

export const getBonusType = () =>
  request({
    prefix,
    url: `/BonusType`,
    params: { hasBTId: false },
    method: 'GET',
  });



