import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Card, Form, Input, Select } from 'antd';
import {
  getDisciplineById,
  updateDiscipline,
  createDiscipline,
  getBonusType,
} from 'services/Discipline';

const { Option } = Select;
const handleInitialValue = initialValue => {
  const initialValueDefault = {
    level: '',
    type: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'formTypeDisciplines' })
class FormTypeDisciplinesPage extends Component {
  state = {
    typeDisciplinesId: false,
    initialValue: {},
    listBonusType: [],
  };

  componentDidMount() {
    this.getData();
  }

  getData = async () => {
    const { match } = this.props;

    const dataListBonusType = await getBonusType();
    this.setState({
      typeDisciplinesId: match.params.id,
      listBonusType: dataListBonusType.data.items,
    });
    if (match.params.id) {
      this.getDataById(match.params.id);
    }
  };

  getDataById = async id => {
    const res = await getDisciplineById(id);
    const initialValue = handleInitialValue(res.data);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      this.saveData(values);
    });
  };

  saveData = async values => {
    const { typeDisciplinesId } = this.state;
    try {
      if (!typeDisciplinesId) {
        await createDiscipline(values);
      } else {
        await updateDiscipline({ id: typeDisciplinesId, ...values });
      }
      this.props.history.goBack();
    } catch (error) {
      console.log(error);
    }
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { typeDisciplinesId, listBonusType } = this.state;
    return (
      <Card title={typeDisciplinesId ? 'Cập nhật xử lý kỹ luật' : 'Thêm xử lý kỹ luật'}>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
            <Form.Item hasFeedback label="Loại xử lý" {...formItemLayout}>
              {getFieldDecorator('label', {
                initialValue: initialValue.label,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Mức xử lý" hasFeedback>
              {getFieldDecorator('level', {
                initialValue: initialValue.level,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <Select style={{ width: '100%' }}>
                  {listBonusType.map(item => (
                    <Option key={item.id} value={item.id}>
                      {item.label}
                    </Option>
                  ))}
                </Select>
              )}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormTypeDisciplinesPage {...props} />
  </Page>
));



